import React from "react";
import DatePicker from "react-datepicker";
import { inject, observer } from "mobx-react";

import "react-datepicker/dist/react-datepicker.css";

// CSS Modules, react-datepicker-cssmodules.css
// import 'react-datepicker/dist/react-datepicker-cssmodules.css';

@inject(["store"])
@observer
export default class Example extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
    };
  }

  handleChange = (date) => {
    this.setState({ date: date });
    // this.state.date = date;
  };

  componentDidUpdate(prevProps) {
    console.log(prevProps.set, this.props.set);
    if (this.props.set == true) {
      console.log("not equal");
      if (this.props.start == true) {
        let firstDate = this.state.date.toISOString();
        let multiDate = firstDate.split("T");
        this.props.store.startDate = multiDate[0] + "00:00:00.000Z";
        this.props.store.date = "Custom";
      }
      if (this.props.start == false) {
        let firstDate = this.state.date.toISOString();
        let multiDate = firstDate.split("T");
        this.props.store.endDate = multiDate[0] + "24:59:99.999Z";
        this.props.store.date = "Custom";
      }
    }
  }

  render() {
    return (
      <DatePicker selected={this.state.date} onChange={this.handleChange} />
    );
  }
}
